.capability {
  position: relative;
  padding-top: 30px;
  
}

.button-donate {
  position: absolute;
  display: block;
  padding: 10px 20px;
  font-size: 25px;
  top: 50%;
  right: 5%;

  border-radius: 30px;
  border: none;
  background-color: orange;
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.button-donate:hover {
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.capability_title {
  font-size: 40px;
  font-weight: normal;
  text-align: center;
}

.capability_content {
  display: flex;
  justify-content: space-evenly;
  padding: 5px 0;

}

.capability_list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  overflow: hidden;
  width: 900px;
  justify-content: space-around;
  margin-top: 30px;
  margin-bottom: 30px;
}

.capability_item {
  display: flex;
  flex-direction: column;
  flex-shrink: unset;
  width: 150px;
  height: 230px;
  padding: 10px;
  background-color: rgba(185, 185, 185, 0.075);
  margin-bottom: 50px;
  margin-left: 30px;
  margin-right: 30px;


  // box-shadow: rgba(0, 0, 0, 0.15) 2.4px 2.4px 3.2px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 15px;
}

